import React from "react";
import {
    Link
  } from "react-router-dom";
import "./style/header.css"

const linkReact = {
    textDecoration: "none",
    color: "white"
}

function Header(){
    return(
        <div className="header">
            <div className="title">
                Piyush Raj
            </div>
            <div className="navbar">
                <ul className="nav">
                    <li><Link to="/" style={linkReact}>About Me</Link></li>
                    <li><Link to="/contact" style={linkReact}>Contact</Link></li>
                </ul>
            </div>
        </div>
    )
}

export default Header;